import RaTe from "../../../components/uni-ui/uni-rate/uni-rate.vue"	
	 export default{
		 components:{
			RaTe 
		 },
		 data(){
			 return{
				 color:'#f2f2f2',
				 activeColor:"#eb828f",
				 size:"30",
				 ss:"",
				 pjlist:[
					 {name:"差"},
					 {name:"一般"},
					 {name:"好"},
					 {name:"很好"},
					 {name:"非常好"},
				 ],
				evalcontent:"",
				imglist:[
					{
					 path:'',
					 imgtrue:false,
					 imgtext:"添加照片"	  
					}
				],
				tslength:"",
				ok:true,
				nmtitle:"你写的评价会以匿名的形式展现",
				src:"",
				srctrue:false,
				change:"非常好",
				change1:"",
				change2:"",
			 }
		 },
		 methods:{
			 chang($event){
			  var  e=$event;
			  var  a=this.watchvalue(e);
			  this.change=a;
	  
			},
			 chang1($event){
				 var  e=$event;
				 var  a=this.watchvalue(e);
				 this.change1=a;
			 },
			 chang2($event){
				 var  e=$event;
				 var  a=this.watchvalue(e);
				 this.change2=a;
			 },
			 watchvalue(e){
				 var title="";	 
				if(e==1){
									title="非常差" 
				}else if(e==2){
									 title="很差"
									 }else if(e==3){
									 title="一般"
									 }else if(e==4){
									 title="很好"
									 }else if(e==5){
									 title="非常好"
									 }
				   return title;
			 },
			deleimg(index){
				this.imgtrue=false;
				this.imglist.splice(index,1);
				var length=this.imglist.length;
				if(length==0){
					var starobj={
					 path:'',
					 imgtrue:false,
					 imgtext:"添加照片"
					};
				 this.imglist.push(starobj);	
				}
				
			 },
			 scimg(){
			 				 var that=this;
			 				 uni.chooseImage({
			 				 	count:5,
			 					sizeType:"compressed",
			 					success:function(res){
			 				    that.imgtrue=true;		
							    var ts=res.tempFiles;
								var tslength=res.tempFiles.length;
									
								for(var i=0; i<ts.length;i++){
									ts[i].imgtrue=true;
								}	
								that.imglist.push(...ts);
								if(tslength<5){
									var enday=[
										{path:"",
										imgtrue:false,
										imgtext:`${tslength}/5`,
										}
									]
								  that.imglist.push(...enday);	
								};
								that.imglist.splice(0,1);
								console.log(that.imglist);
			 					}
			 				 })
			 				 
							 /*else{
			 				  uni.previewImage({
			 				              urls:that.imglist,
			 								   })
			 				 }*/
			 },
			 scvideo(){
				    var that=this;
				           uni.chooseVideo({
				                           count: 1,
				                           sourceType: ['camera', 'album'],
				                           success: function (res) {
											   that.srctrue=true;
				                               that.src = res.tempFilePath;
				                           }
				                       });
				      
				        
				    
			 },
			 nmchange(){
				 this.ok=!this.ok;
			  	 if(this.ok==true){
					 this.nmtitle="你写的评价会以匿名的形式展现"
				 }else{
					this.ok==false;
					this.nmtitle="公开的评价会展示在个人页面"
				 }
			 },
			 
		 }
		 
		}
	 
